import { Card, Button, Spinner } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import React, { useCallback, useEffect, useState } from "react";
import axios from "axios";
import { NotificationManager } from "react-notifications";
import PlaceHolderImage from "../../assets/images/avatar.png";
import bearxabi from "../../assets/BearXABI.json";
import stakingabi from "../../assets/stakingABI.json";
import Web3 from "web3";
import contract from "../../config/contract";
import { BigNumber } from "@ethersproject/bignumber";

function BearXCard(props) {
  const {
    tokenId,
    staked,
    account,
    processed,
    clickable,
    clicked,
    disabled,
    loading,
    launchState,
    disLaunchState,
  } = props;
  const [name, setName] = useState("");
  const [image, setImage] = useState("");
  const [descript, setDescript] = useState("");
  const [MyWeb3, setMyWeb3] = useState([]);
  const [myAccount, setMyAccount] = useState([]);
  const [pending, setPending] = useState(false);

  const setLoading = (loading) => {
    sessionStorage.setItem("isLoading", loading);
  };
  const getLoading = () => {
    return sessionStorage.getItem("isLoading");
  };
  const isSpecial = (_id) => {
    if (_id >= 1000000000000 && _id <= 1000000000005) return true;
    return false;
  };

  useEffect(() => {
    if (window.web3 != undefined && window.ethereum) {
      loadWeb3();
    }
  }, []);

  useEffect(() => {
    if (MyWeb3.length !== 0) {
      bearxcardintitial();
    }
  }, [MyWeb3]);

  async function loadWeb3() {
    const web3 = await new Web3(window.ethereum);
    await web3.eth
      .getAccounts()
      .then((acc) => {
        setMyWeb3(web3);
        setMyAccount(acc);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async function bearxcardintitial() {
    const BearXcontract = await new MyWeb3.eth.Contract(
      bearxabi,
      contract.BearX[1]
    );
    await BearXcontract.methods
      .tokenURI(tokenId)
      .call()
      .then((r) => {
        axios.get(r.toString()).then((res) => {
          console.log(res);
          setName(res.data.name);
          setImage(res.data.image);
          setDescript(res.data.description);
        });
      });
  }

  const stake = useCallback(
    async (id, account) => {
      setPending(true);
      setLoading(true);
      launchState();
      console.log(account);

      try {
        // const BearXcontract = await new MyWeb3.eth.Contract(
        //   bearxabi,
        //   contract.BearX[1]
        // );

        const stakingcontract = await new MyWeb3.eth.Contract(
          stakingabi,
          contract.BearXStaking[1]
        );
        // console.log("clicked");
        // BearXcontract.methods
        //   .approve(contract.BearXStaking[1], id)
        //   .send({ from: account })
        //   .then((e) => {
        //     console.log("Approved Successfully!");
        //     NotificationManager.success("Approved Successfully!");
        const ids = [];
        ids.push(id);
        console.log([ids]);
        stakingcontract.methods
          .createStake(ids)
          .send({ from: account })
          .then((e) => {
            setPending(false);
            processed();
            console.log("Staked Successfully!");
            NotificationManager.success("Staked Successfully!");
            window.location.reload();
            // clicked(false);
          })
          .catch((err) => {
            processed();
            console.log(err);
            setPending(false);
            // clicked(false);
            disLaunchState();
          });
          // })
          // .catch((err) => {
          //   console.log(err);
          //   setPending(false);
          //   // clicked(false);
          //   disLaunchState();
          // });
      } catch (err) {
        console.log(err);
        setPending(false);
        // clicked(false);
        disLaunchState();
      }
    },
    [MyWeb3]
  );

  const unstake = useCallback(
    async (_ids, account) => {
      launchState();
      setLoading(true);
      console.log(_ids);
      //   return;
      setPending(true);
      const ids = [..._ids];
      console.log(account);
      console.log([ids]);
      try {
        const stakingcontract = await new MyWeb3.eth.Contract(
          stakingabi,
          contract.BearXStaking[1]
        );
        console.log("clicked");
        stakingcontract.methods
          .unStake(ids)
          .send({ from: account })
          .then((e) => {
            setPending(false);
            // clicked(false);
            NotificationManager.success("Untaked Successfully!");
            console.log("Untaked Successfully!");
            processed();
            window.location.reload();
          })
          .catch((err) => {
            console.log(err);
            setPending(false);
            // clicked(false);
            // disLaunchState();
          });
      } catch (err) {
        console.log(err);
        setPending(false);
        // clicked(false);
        disLaunchState();
      }
    },
    [MyWeb3]
  );

  useEffect(() => {}, []);

  return (
    <Card
      className="bearx-card"
      style={{
        width: "20%",
        margin: "5px",
        display: "flex",
        backgroundColor: "#14181d",
      }}
    >
      {staked ? (
        <div className="ribbon-wrapper">
          <div className="ribbon">STAKED</div>
        </div>
      ) : isSpecial(tokenId) ? (
        <div className="ribbon-wrapper">
          <div className="ribbon">1/1</div>
        </div>
      ) : (
        <></>
      )}
      <Card.Img
        variant="top"
        src={image}
        onError={() => setImage(PlaceHolderImage)}
      />
      <Card.Body>
        <Card.Title className="card_font" style={{ color: "white" }}>
          {name}
        </Card.Title>
        <div className="buttons">
          {staked ? (
            pending ? (
              <button
                type="button"
                className="button card_font"
                disabled={loading}
              >
                <Spinner
                  as="span"
                  variant="light"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                  animation="border"
                />
              </button>
            ) : (
              <button
                type="button"
                className="button card_font"
                onClick={() => {
                  unstake([tokenId], account);
                  //   clicked(true);
                }}
                disabled={loading}
              >
                Unstake
              </button>
            )
          ) : pending ? (
            <button
              type="button"
              className="button card_font card_fontsize_smaller"
              disabled={loading}
            >
              <Spinner
                as="span"
                variant="light"
                size="sm"
                role="status"
                aria-hidden="true"
                animation="border"
              />
            </button>
          ) : (
            <button
              type="button"
              className="button card_font card_fontsize_smaller"
              onClick={() => {
                stake(tokenId, account);
                // clicked(true);
              }}
              disabled={loading}
            >
              Stake
            </button>
          )}
        </div>
      </Card.Body>
    </Card>
  );
}

export default BearXCard;
