import React, { useCallback, useEffect, useState } from "react";
import Web3 from "web3";
import { Spinner } from "react-bootstrap";
import stakingabi from "../assets/stakingABI.json";
import bearxabi from "../assets/BearXABI.json";
import contract from "../config/contract";
import { NotificationManager } from "react-notifications";

function Rewards() {
  const [MyWeb3, setMyWeb3] = useState([]);
  const [myAccount, setMyAccount] = useState([]);
  const [totalStaked, setTotalStaked] = useState(0);
  const [youStaked, setYouStaked] = useState(0);
  const [claimableROOTx, setClaimableROOTx] = useState(0);
  const [claimableSROOTx, setClaimableSROOTx] = useState(0);
  const [claimableETH, setClaimableETH] = useState(0);
  const [isVested, SetVested] = useState(true);
  const [pending, setPending] = useState(false);
  const [claimROOT, setClaimROOT] = useState(false);
  const [claimSROOT, setClaimSROOT] = useState(false);
  const [claimWETH, setClaimWETH] = useState(false);

  useEffect(() => {
    if (window.web3 != undefined && window.ethereum) {
      loadWeb3();
    }
  }, []);

  useEffect(() => {
    if (MyWeb3.length !== 0) {
      gettotalstake();
      getyoustake();
      getClaimOf();
      getVested();
    }
  }, [MyWeb3, myAccount[0]]);

  async function loadWeb3() {
    const web3 = await new Web3(window.ethereum);
    await web3.eth
      .getAccounts()
      .then((acc) => {
        setMyWeb3(web3);

        setMyAccount(acc);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async function gettotalstake() {
    if (myAccount.length === 0) return;

    const stakingcontract = await new MyWeb3.eth.Contract(
      stakingabi,
      contract.BearXStaking[1]
    );

    await stakingcontract.methods
      .totalStakes()
      .call()
      .then((r) => {
        setTotalStaked(parseInt(r.toString()));
      })
      .catch((err) => {
        setTotalStaked(0);
        console.log(err);
      });
  }

  async function getyoustake() {
    if (myAccount.length === 0) return;

    const stakingcontract = await new MyWeb3.eth.Contract(
      stakingabi,
      contract.BearXStaking[1]
    );

    await stakingcontract.methods
      .stakeOf(myAccount[0])
      .call()
      .then((r) => {
        setYouStaked(r.length);
      })
      .catch((err) => {
        setYouStaked(0);
        console.log(err);
      });
  }

  async function getClaimOf() {
    if (myAccount.length === 0) return;

    const stakingcontract = await new MyWeb3.eth.Contract(
      stakingabi,
      contract.BearXStaking[1]
    );

    await stakingcontract.methods
      .claimOf(myAccount[0])
      .call()
      .then((r) => {
        setClaimableROOTx(parseInt(r[0].toString()) / 10 ** 18);
        setClaimableSROOTx(parseInt(r[1].toString()) / 10 ** 18);
        setClaimableETH(parseInt(r[2].toString()) / 10 ** 18);
      })
      .catch((err) => {
        setClaimableROOTx(0);
        setClaimableSROOTx(0);
        setClaimableETH(0);
      });
  }

  async function getVested() {
    if (myAccount.length === 0) return;

    const stakingcontract = await new MyWeb3.eth.Contract(
      stakingabi,
      contract.BearXStaking[1]
    );

    await stakingcontract.methods
      .isVested(myAccount[0])
      .call()
      .then((r) => {
        console.log("ISVESTED", r);
        SetVested(r);
      })
      .catch((err) => {
        console.log("ISVESTED", err);
        SetVested(true);
      });
  }

  const claimOfROOTxToken = useCallback(async () => {
    if (myAccount[0]) {
      setClaimROOT(true);

      try {
        const stakingcontract = await new MyWeb3.eth.Contract(
          stakingabi,
          contract.BearXStaking[1]
        );
        stakingcontract.methods
          .claimOfROOTxToken()
          .send({ from: myAccount[0] })
          .then((e) => {
            if (e.status) {
              NotificationManager.success("You claimed Successfully.");
              setClaimROOT(false);
              console.log("You claimed Successfully.");
              window.location.reload();
            } else {
              NotificationManager.error("You claimed Failed.");
              setClaimROOT(false);
              console.log("You claimed Failed.");
            }
          })
          .catch((e) => {
            console.log("You claimed Failed");
            setClaimROOT(false);
            console.log(e);
          });
      } catch (err) {
        setClaimROOT(false);
      }
    }
  }, [MyWeb3, myAccount[0]]);

  const claimOfSROOTxToken = useCallback(async () => {
    if (myAccount[0]) {
      setClaimSROOT(true);

      try {
        const stakingcontract = await new MyWeb3.eth.Contract(
          stakingabi,
          contract.BearXStaking[1]
        );
        stakingcontract.methods
          .claimOfSROOTxToken()
          .send({ from: myAccount[0] })
          .then((e) => {
            console.log(e.status);
            if (e.status) {
              NotificationManager.success("You claimed Successfully.");
              setClaimSROOT(false);
              console.log("You claimed Successfully.");
              window.location.reload();
            } else {
              NotificationManager.error("You claimed Failed.");
              setClaimSROOT(false);
              console.log("You claimed Failed.");
            }
          })
          .catch((e) => {
            console.log("You claimed Failed");
            setClaimSROOT(false);
            console.log(e);
          });
      } catch (err) {
        setClaimSROOT(false);
      }
    }
  }, [MyWeb3, myAccount[0]]);

  const claimOfWETH = useCallback(async () => {
    if (myAccount[0]) {
      setClaimWETH(true);

      try {
        const stakingcontract = await new MyWeb3.eth.Contract(
          stakingabi,
          contract.BearXStaking[1]
        );
        stakingcontract.methods
          .claimOfWETH()
          .send({ from: myAccount[0] })
          .then((e) => {
            console.log(e.status);
            if (e.status) {
              NotificationManager.success("You claimed Successfully.");
              setClaimWETH(false);
              console.log("You claimed Successfully.");
              window.location.reload();
            } else {
              NotificationManager.error("You claimed Failed.");
              setClaimWETH(false);
              console.log("You claimed Failed.");
            }
          })
          .catch((e) => {
            console.log("You claimed Failed");
            setClaimWETH(false);
            console.log(e);
          });
      } catch (err) {
        setClaimWETH(false);
      }
    }
  }, [MyWeb3, myAccount[0]]);

  return (
    <div className="claim">
      <div className="claim-rootx">
        <div className="claim-rootx-container">
          <div className="claim-rootx-title">CLAIM BEARX</div>
          <div className="claim-rootx-mainboard">
            <div className="claim-rootx-yield">
              {myAccount[0] ? (
                <>
                  <div className="claim-rootx-yield-dashboard">
                    <div className="claim-rootx-yield-staked-subdashboard">
                      <div className="claim-rootx-yield-dashboard-item">
                        <div className="claim-grey-color">Total Staked</div>
                        <div className="status-value">{totalStaked}</div>
                      </div>
                      <div className="claim-rootx-yield-dashboard-item">
                        <div className="claim-grey-color">You Staked</div>
                        <div className="status-value">{youStaked}</div>
                      </div>
                    </div>
                    <div className="claim-rootx-yield-Rewards-subdashboard">
                      <div className="claim-rootx-yield-dashboard-item">
                        <div className="claim-grey-color reward special-title">
                          Claimable ROOTx
                        </div>
                        <div className="status-value reward">
                          {claimableROOTx.toFixed(3)}
                        </div>
                        {!claimROOT ? (
                          <button
                            className="claim-rootx-button"
                            type="button"
                            onClick={() => claimOfROOTxToken()}
                          >
                            CLAIM ROOTx
                          </button>
                        ) : (
                          <button className="claim-rootx-button" type="button">
                            <Spinner
                              as="span"
                              variant="light"
                              size="sm"
                              role="status"
                              aria-hidden="true"
                              animation="border"
                            />
                          </button>
                        )}
                      </div>
                      <div className="claim-rootx-yield-dashboard-item">
                        <div className="claim-grey-color reward special-title">
                          Claimable SROOTx
                        </div>
                        <div className="status-value reward">
                          {claimableSROOTx.toFixed(3)}
                        </div>

                        {isVested ? (
                          <div className="claim-rootx-button">Vested</div>
                        ) : !claimSROOT ? (
                          <button
                            className="claim-rootx-button"
                            type="button"
                            onClick={() => claimOfSROOTxToken()}
                          >
                            CLAIM SROOTx
                          </button>
                        ) : (
                          <button className="claim-rootx-button" type="button">
                            <Spinner
                              as="span"
                              variant="light"
                              size="sm"
                              role="status"
                              aria-hidden="true"
                              animation="border"
                            />
                          </button>
                        )}
                      </div>
                      <div className="claim-rootx-yield-dashboard-item">
                        <div className="claim-grey-color reward special-title">
                          Amount of SROOTx
                          <br />
                          (claimable in WETH)
                        </div>
                        <div className="status-value reward">
                          {claimableETH.toLocaleString("en-US", {
                            maximumFractionDigits: 7,
                          })}
                        </div>

                        {isVested ? (
                          <div className="claim-rootx-button">Vested</div>
                        ) : !claimWETH ? (
                          <button
                            className="claim-rootx-button"
                            type="button"
                            onClick={() => claimOfWETH()}
                          >
                            CLAIM WETH
                          </button>
                        ) : (
                          <button className="claim-rootx-button" type="button">
                            <Spinner
                              as="span"
                              variant="light"
                              size="sm"
                              role="status"
                              aria-hidden="true"
                              animation="border"
                            />
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                </>
              ) : (
                <div className="claim-warn">Please connect your wallet.</div>
              )}
            </div>
            <div className="claim-rootx-yield-description">
              {myAccount[0] ? (
                <>
                  <div className="claim-grey-color-description">
                    <p className="normal-text">
                      🐻 Earn WETH + SROOT by staking
                    </p>
                    <p className="normal-text">
                      🐻 Stake 5+ Genesis Bear and yield 50% SROOTx + 50% SROOTx
                      rewarded in WETH.
                    </p>
                    <p className="normal-text">
                      🐻 Earn SROOT by providing liquidity to the SROOTx - ETH
                      pair via Uniswap V2 to earn additional rewards on top of
                      Uniswap’s regular liquidity provider rewards.(Coming Soon)
                    </p>
                  </div>
                </>
              ) : (
                <div className="claim-warn-description">Loading...</div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Rewards;
