import React, { useState, useEffect } from 'react'
import image1 from '../assets/images/minibearx/1.jpeg'
import image2 from '../assets/images/minibearx/2.jpeg'
import image3 from '../assets/images/minibearx/3.jpeg'
import image4 from '../assets/images/minibearx/4.jpeg'
import image5 from '../assets/images/minibearx/5.jpeg'
import image6 from '../assets/images/minibearx/6.jpeg'
import sectionGif from '../assets/images/minibearx/banner.gif'
import plus from '../assets/images/minibearx/plus.png'


import Web3 from 'web3';
import RAbi from "../assets/rootABI.json";
import abi from "../assets/BearXABI.json";


function MiniBearx() {
    const [ myAccount, setMyAccount ] = useState("");
    const [ MyWeb3, setMyWeb3 ] = useState([]);
    const [ MyContract, setMyContract ] = useState([]);
    const [ Tokens, setTokens ] = useState(0);
    const [ toClaim, setToClaim ] = useState("");

    useEffect( ()=>  {
        loadWeb3()
    }, [])


    async function loadWeb3() {
        if(window.web3 !=  undefined && window.ethereum){
            const web3 = await new Web3(window.ethereum)
            await web3.eth.getAccounts()
            .then((acc) => {
                setMyWeb3(web3)
                setMyAccount(acc)

            }).catch((err) => {
                console.log(err);
            });
        }
    }

    async function contract123() {
        const contract1 = await new MyWeb3.eth.Contract(abi, "0xE22e1e620dffb03065CD77dB0162249c0c91bf01")
        // console.log(contract1);
        // console.log(myAccount);

        if (myAccount.length == 0) {
            alert("Connect Wallet First.")
            await loadWeb3()
            // return false;
        } else {

            await contract1.methods.walletOfOwner(myAccount[0]).call()
            // await contract1.methods.walletOfOwner("0x1692ab2f357c5979f879f1e46f781048b95f3977").call()
            .then( async  (NFTs) => {
                console.log("NFTs");
                console.log(NFTs);

                if(NFTs.length >= 2){
                    console.log(NFTs);

                    await contract1.methods.MINT_BY_TOKEN(NFTs[0], NFTs[1]).send({from: myAccount[0]})
                    .then((result) => {
                        alert("Token Minted");
                        // console.log(result);
                    }).catch((err) => {
                        console.log(err);
                    });

                } else {
                    alert("You don't have enough Bears.")
                }
            }).catch((err) => {
                console.log(err);
            });
        }
    }

    async function Approval(){
        const RContract = await new MyWeb3.eth.Contract(RAbi, "0xd718Ad25285d65eF4D79262a6CD3AEA6A8e01023")
        if (myAccount.length == 0) {
            alert("Connect Wallet First.")
            await loadWeb3()
            // return false;
        } else {

            await RContract.methods.approve("0xE22e1e620dffb03065CD77dB0162249c0c91bf01", "100000000000000000000000").send({from: myAccount[0]})
            // await contract1.methods.walletOfOwner("0x1692ab2f357c5979f879f1e46f781048b95f3977").call()
            .then( async  (NFTs) => {
                alert("Now you have approval to mint new MiniBearX.")
            }).catch((err) => {
                console.log(err);
            });
        }

    }

    return (
        <div id="miniBearX">
            <div className="container">
                <div className="miniBearX__banner">
                    <h2>MINI BEARX</h2>
                    <div className="miniBearX__imagebox">
                        <img src={image3} alt="MiniBearX"/>
                        <img src={image2} alt="MiniBearX"/>
                        <img src={image1} alt="MiniBearX"/>
                        <img src={image4} alt="MiniBearX"/>
                    </div>
                </div>
                <div className="miniBearX__content">
                    <h2 className="section__heading">5,300 MINI BEARS ENTER THE UNIVERSE</h2>
                </div>
                <div className="row align-items-center">
                    <div className="col-lg-4">
                        <img className="gifImageSize" src={sectionGif} alt="Mini BearXchallenges"/>
                    </div>
                    <div className="col-lg-8">
                    <p style={{marginTop:0}} className="miniBearx__paragraph">MiniBearX is the second collection launch from BearX Labs. They are the spawn of the genesis Bears and act as the future hope for the species. There are only 5,300 MiniBears, but they come with a host of benefits.</p>
                    <p className="miniBearx__paragraph">
                    <b>To mint, you need (in your wallet):</b><br/>
                    - 2 Genesis BearX NFTs<br/>
                    - 1000 ROOT tokens (claimed)
                    </p>
                    <p className="miniBearx__paragraph">Genesis Bears can have multiple offspring, so there is no limit to the amount 2 Genesis Bears can breed. The quest for BearX dominance in this world continues...</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-12">
                        <div className="burn__button">
                            <button onClick={ () => Approval() }>Approval for Minting</button>
                        </div>
                        <br />
                        <br />

                        <div className="section__heading">
                            BREED
                        </div>
                        <div className="burn__image">
                            <img src={image5} alt="BREED"/>
                            <img style={{width:'100px',height:'100px',marginTop:'50px'}} src={plus} alt="BREED"/>
                            <img src={image6} alt="BREED"/>
                        </div>
                        <div className="burn__button">
                            <button onClick={ () => contract123() }>MINT AN INCUBATOR</button>
                        </div>                        
                            <h5 style={{ color:  "white"}}>
                                Please click the APPROVE first (you only have to do this once), then click MINT AN INCUBATOR. <br />
                                once you mint, you will burn 1000 ROOT tokens in the process, and receive an BearX incubator. All bears will come out of incubation on January 2022.
                            </h5>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MiniBearx
