import React, { useCallback, useEffect, useState } from "react";
import Web3 from "web3";
import { Spinner } from "react-bootstrap";
import stakingabi from "../assets/stakingABI.json";
import bearxabi from "../assets/BearXABI.json";
import rootxabi from "../assets/rootABI.json";
import contract from "../config/contract";
import { NotificationManager } from "react-notifications";
import { BigNumber, ethers } from "ethers";

function Rewards() {
  const [MyWeb3, setMyWeb3] = useState([]);
  const [myAccount, setMyAccount] = useState([]);
  const [maxROOTx, setMaxROOTx] = useState(1);
  const [ROOTx, setROOTx] = useState(0);
  const [maxbtnpending, setMaxbtnpending] = useState(false);
  const [burnbtnpending, setBurnbtnpending] = useState(false);
  const [approvebtnpending, setApprovebtnpending] = useState(false);

  const setIMaxROOTx = (value) => {
    if (maxROOTx >= 1) {
      if (value == 0) return;
      setMaxROOTx(value);
    }
  };

  useEffect(() => {
    if (window.web3 != undefined && window.ethereum) {
      loadWeb3();
    }
  }, []);

  useEffect(() => {
    if (MyWeb3.length !== 0) {
      getbalanceOfrootx();
    }
  }, [MyWeb3, myAccount[0]]);

  async function loadWeb3() {
    const web3 = await new Web3(window.ethereum);
    await web3.eth
      .getAccounts()
      .then((acc) => {
        setMyWeb3(web3);

        setMyAccount(acc);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async function getbalanceOfrootx() {
    if (myAccount.length === 0) return;

    const rootxcontract = await new MyWeb3.eth.Contract(
      rootxabi,
      contract.ROOTx[1]
    );

    rootxcontract.methods
      .balanceOf(myAccount[0])
      .call()
      .then((r) => {
        var result = r;
        setROOTx(result);
      })
      .catch((err) => {
        setROOTx(0);
        console.log(err);
      });
  }

  async function getmaxrootx() {
    if (myAccount.length === 0) return;

    setMaxbtnpending(true);
    const rootxcontract = await new MyWeb3.eth.Contract(
      rootxabi,
      contract.ROOTx[1]
    );

    rootxcontract.methods
      .balanceOf(myAccount[0])
      .call()
      .then((r) => {
        var result = r / 1000000000000000000;
        setMaxROOTx(result);
        setMaxbtnpending(false);
      })
      .catch((err) => {
        setMaxROOTx(0);
        console.log(err);
        setMaxbtnpending(false);
      });
  }

  async function burnrootx() {
    if (myAccount.length === 0) return;

    setBurnbtnpending(true);

    const stakingcontract = await new MyWeb3.eth.Contract(
      stakingabi,
      contract.BearXStaking[1]
    );

    let valueofrootx = document.getElementById("value_rootx").value;
    // var temp = BigNumber.from(String(1000000000000000000 * valueofrootx))
    // var temp = BigNumber.from(Math.pow(10, 18) * valueofrootx);
    var bg_decimal = BigNumber.from("1000000000000000000");
    var bg_valueofrootx = BigNumber.from(valueofrootx);

    var temp = bg_decimal.mul(bg_valueofrootx);
    console.log("========>" + temp.toString());

    if (ROOTx != 0) {
      stakingcontract.methods
        .BurnRootx_mintSrootx(temp)
        .send({ from: myAccount[0] })
        .then((res) => {
          console.log("Success!", res);
          setBurnbtnpending(false);
          alert("Success!");
        })
        .catch((err) => {
          console.log(err);
          alert("failed Burn!");
          setBurnbtnpending(false);
        });
    } else {
      setBurnbtnpending(false);
      alert("insufficient balance!");
      return;
    }
  }

  async function approverootx() {
    if (myAccount.length === 0) return;

    setApprovebtnpending(true);

    const rootxcontract = await new MyWeb3.eth.Contract(
      rootxabi,
      contract.ROOTx[1]
    );
    console.log("XXXXX====>>>", ROOTx);

    if (ROOTx != 0) {
      try {
        rootxcontract.methods
          .approve(contract.BearXStaking[1], ROOTx)
          .send({ from: myAccount[0] })
          .then(async (r) => {
            setApprovebtnpending(false);
            alert("Approve Success!");
            console.log("Approve Success", r);
          })
          .catch((err) => {
            setApprovebtnpending(false);
            console.log("Approve Failed!", err);
            alert("Approve Failed!");
          });
      } catch (err) {
        console.log("error====>>>>", err);
      }
    }
  }

  return (
    <div className="claim">
      <div className="claim-rootx">
        <div className="claim-rootx-container">
          <div className="claim-rootx-title">Burn ROOTx, Get SROOTx</div>
          <div className="burn-rootx-mainboard">
            <div className="burn-rootx-yield-description">
              {myAccount[0] ? (
                <>
                  <div className="claim-grey-color-description">
                    <p className="normal-text">
                      🐻 Burn 1 ROOTx and Get 5 SROOTx
                    </p>
                    <div className="burn-get-maincontain">
                      <div className="burn-get-subcontain">
                        <div className="number">
                          <button
                            class="number-minus"
                            type="button"
                            onClick={() => setIMaxROOTx(maxROOTx - 1)}
                          >
                            -
                          </button>
                          <input
                            type="number"
                            placeholder="0"
                            min={1}
                            id="value_rootx"
                            value={maxROOTx}
                            onChange={(e) => setMaxROOTx(e.target.value)}
                          />
                          <button
                            class="number-plus"
                            type="button"
                            onClick={() => setIMaxROOTx(maxROOTx + 1)}
                          >
                            +
                          </button>
                        </div>
                        {maxbtnpending ? (
                          <button className="max-rootx-button" type="button">
                            <Spinner
                              as="span"
                              variant="light"
                              size="sm"
                              role="status"
                              aria-hidden="true"
                              animation="border"
                            />
                          </button>
                        ) : (
                          <button
                            className="max-rootx-button"
                            type="button"
                            onClick={getmaxrootx}
                          >
                            Max
                          </button>
                        )}
                      </div>
                      <div className="burn-btn-get-subcontain">
                        {approvebtnpending ? (
                          <button
                            className="approve-rootx-button-pending"
                            type="button"
                          >
                            <Spinner
                              as="span"
                              variant="light"
                              size="sm"
                              role="status"
                              aria-hidden="true"
                              animation="border"
                            />
                          </button>
                        ) : burnbtnpending ? (
                          <button
                            className="approve-rootx-button"
                            type="button"
                            onClick={approverootx}
                            disabled
                          >
                            Approve ALL
                          </button>
                        ) : (
                          <button
                            className="approve-rootx-button"
                            type="button"
                            onClick={approverootx}
                          >
                            Approve ALL
                          </button>
                        )}

                        {burnbtnpending ? (
                          <button className="burn-rootx-button" type="button">
                            <Spinner
                              as="span"
                              variant="light"
                              size="sm"
                              role="status"
                              aria-hidden="true"
                              animation="border"
                            />
                          </button>
                        ) : approvebtnpending ? (
                          <button
                            className="burn-rootx-button"
                            type="button"
                            onClick={burnrootx}
                            disabled
                          >
                            Burn
                          </button>
                        ) : (
                          <button
                            className="burn-rootx-button"
                            type="button"
                            onClick={burnrootx}
                          >
                            Burn
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                </>
              ) : (
                <div className="claim-warn-description">Loading...</div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Rewards;
