import React, { useCallback, useEffect, useRef, useState } from "react";
import BearXCard from "../components/Cards/BearXCard";
import GifIcon from "../assets/images/mint.a0a2fe97.gif";
import { useWeb3React } from "@web3-react/core";
import { NotificationManager } from "react-notifications";
import { Spinner } from "react-bootstrap";
import Web3 from "web3";
import Swal from "sweetalert2";
import srootAbi from "../assets/srootxABI.json";
import stakingabi from "../assets/stakingABI.json";
import bearxabi from "../assets/BearXABI.json";
import Bear1 from "../assets/images/nfts/bearx/bear1.jpg";
import { unstable_renderSubtreeIntoContainer } from "react-dom";
import Rewards from "./Reward";
import contract from "../config/contract";
import BearXWorld01 from "../assets/images/BearXWorld01.png";
import BearXWorld02 from "../assets/images/BearXWorld02.png";


function BearXSTEAK() {
  const [MyWeb3, setMyWeb3] = useState([]);
  const [myAccount, setMyAccount] = useState([]);
  const [SROOTxTokenAPR, setSROOTxTokenAPR] = useState(0);
  const [bearxBalance, setBearxBalance] = useState(0);
  const [bearxTokenIds, setBearxTokenIds] = useState([]);
  const _bearxTokenIds = useRef([]);
  const [pending, setPending] = useState(false);
  const [bearxTokenIdsStaked, setBearxTokenIdsStaked] = useState([]);
  const [bearXOfOwner, setBearXOfOwner] = useState([]);
  const _bearxTokenIdsStaked = useRef([]);
  const [unstakepending, setunstakepending] = useState(false);
  const [stakepending, setStakePending] = useState(false);
  const [loading, setLoading] = useState(false);
  const [approved, setApproved] = useState(false);

  const launchState = () => {
    setLoading(true);
  };
  const disLaunchState = () => {
    setLoading(false);
  };

  useEffect(() => {
    if (window.web3 != undefined && window.ethereum) {
      loadWeb3();
    }
  }, []);

  useEffect(() => {
    if (MyWeb3.length !== 0) {
      getAPR();
      stakeof();
      BearXInitial();
      getwalletOfOwner();
    }
  }, [MyWeb3, myAccount[0]]);

  async function loadWeb3() {
    const web3 = await new Web3(window.ethereum);
    await web3.eth
      .getAccounts()
      .then((acc) => {
        setMyWeb3(web3);
        setMyAccount(acc);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async function stakeof() {
    if (myAccount.length === 0) return;
    console.log(myAccount[0]);

    const stakingcontract = await new MyWeb3.eth.Contract(
      stakingabi,
      contract.BearXStaking[1]
    );
    await stakingcontract.methods
      .stakeOf(myAccount[0])
      .call()
      .then((r) => {
        console.log("stakeof", r.length);
        setBearxTokenIdsStaked((b) => r);
        console.log(parseInt(r[0]));
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async function getAPR() {
    const stakingcontract = await new MyWeb3.eth.Contract(
      stakingabi,
      contract.BearXStaking[1]
    );

    await stakingcontract.methods
      .get_APR()
      .call()
      .then((r) => {
        setSROOTxTokenAPR(Number(r) / 10 ** 18);
        console.log(r);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async function getwalletOfOwner() {
    if (myAccount.length === 0) return;
    const BearXcontract = await new MyWeb3.eth.Contract(
      bearxabi,
      contract.BearX[1]
    );
    try {
      await BearXcontract.methods
        .walletOfOwner(myAccount[0])
        .call()
        .then((r) => {
          setBearXOfOwner((b) => r);
          console.log(r);
        })
        .catch((err) => {
          console.log(err);
        });
    } catch (err) {
      console.log(err);
    }
  }

  const unstake = useCallback(
    async (_ids, account) => {
      console.log(_ids);
      //   return;
      setunstakepending(true);
      const ids = [..._ids];
      console.log(ids);
      console.log(account);
      try {
        const stakingcontract = await new MyWeb3.eth.Contract(
          stakingabi,
          contract.BearXStaking[1]
        );
        console.log("clicked");
        stakingcontract.methods
          .unStake(ids)
          .send({ from: account })
          .then((e) => {
            setunstakepending(false);
            NotificationManager.success("Untaked Successfully!");
            console.log("Untaked Successfully!");
            window.location.reload();
          })
          .catch((err) => {
            console.log(err);
            setunstakepending(false);
          });
      } catch (err) {
        console.log(err);
        setunstakepending(false);
      }
    },
    [MyWeb3]
  );

  const stake = useCallback(
    async (id, account) => {
      setStakePending(true);
      setLoading(true);
      launchState();

      try {
        const stakingcontract = await new MyWeb3.eth.Contract(
          stakingabi,
          contract.BearXStaking[1]
        );

        const ids = [...id];

        console.log(
          "IDS=====>>>>>>",
          ids,
          "Account======>>>>>",
          account,
          "contracts==========>",
          stakingcontract
        );
        stakingcontract.methods
          .createStake(ids)
          .send({ from: account })
          .then((e) => {
            setStakePending(false);
            console.log("Staked Successfully!");
            NotificationManager.success("Staked Successfully!");
            window.location.reload();
          })
          .catch((err) => {
            console.log(err);
            setStakePending(false);
            disLaunchState();
          });
      } catch (err) {
        console.log("XXX=============>>>>>>", err);
        setStakePending(false);
        // clicked(false);
        disLaunchState();
      }
    },
    [MyWeb3]
  );

  async function BearXInitial() {
    if (myAccount.length === 0) return;

    const BearXcontract = new MyWeb3.eth.Contract(bearxabi, contract.BearX[1]);
    await BearXcontract.methods
      .balanceOf(myAccount[0])
      .call()
      .then((r) => {
        console.log("xxxxx =====", r);

        setBearxBalance(parseInt(r.toString()));
        _bearxTokenIds.current = [];
        setBearxTokenIds((b) => []);
        console.log("balance ==== ", bearxBalance, r.toString());
        console.log("account", myAccount[0]);
        const tempB = parseInt(r.toString());
        const tempBs = [];
        for (let i = 0; i < parseInt(r.toString()); i++) {
          BearXcontract.methods
            .tokenOfOwnerByIndex(myAccount[0], i)
            .call()
            .then((rid) => {
              console.log("yyyyyyyy =====", rid);
              tempBs.push(parseInt(rid.toString()));
              _bearxTokenIds.current.push([
                parseInt(rid.toString()),
                ..._bearxTokenIds.current,
              ]);
              console.log("tempbs", ...tempBs);
              if (tempBs.length === tempB) {
                console.log("zzzzz ===== ", tempBs);
                setBearxTokenIds(tempBs);
              }
              console.log("_bearxTokenIds", _bearxTokenIds);
            })
            .catch((err) => {
              console.log(err);
            });
        }
        console.log("BearInitial5");
      })
      .catch((err) => {
        console.log(err);
      });
    console.log("BearInitial6");
  }

  async function getbearxofwallet() {
    if (myAccount.length === 0) return;

    const web3 = new Web3(window.ethereum);
    let nftcounts = 0;
    let isapproved = false;
    const BearXcontract = new web3.eth.Contract(bearxabi, contract.BearX[1]);
    try {
      await BearXcontract.methods
        .balanceOf(myAccount[0])
        .call()
        .then((r) => {
          console.log("count of bear", r, myAccount[0]);
          nftcounts = r;
        })
        .catch((err) => {
          console.log("Parse Error", err);
        });
    } catch (err) {
      console.log("Big Error", myAccount[0], err);
    }

    try {
      await BearXcontract.methods
        .isApprovedForAll(myAccount[0], contract.BearXStaking[1])
        .call()
        .then((r) => {
          setApproved(r);
        })
        .catch((err) => {
          console.log("Parse Error", err);
        });
    } catch (err) {
      console.log("Big Error", myAccount[0], err);
    }
    if (!approved && nftcounts != 0) confirmModal();
  }

  const confirmModal = () => {
    Swal.fire({
      icon: "question",
      title: "Confirm Approve",
      text: "Do you want to approve all bears to staking contract?",
      showCancelButton: true,
    }).then((res) => {
      if (res.isConfirmed) {
        setApprovalforall();
      }
    });
    // if (window.ethereum.selectedAddress || nftcounts != 0 || !approved)
  };

  const setApprovalforall = useCallback(async () => {
    if (myAccount.length === 0) return;
    console.log(myAccount[0], approved);
    try {
      const web3 = new Web3(window.ethereum);
      const BearXcontract = await new web3.eth.Contract(
        bearxabi,
        contract.BearX[1]
      );
      BearXcontract.methods
        .setApprovalForAll(contract.BearXStaking[1], true)
        .send({ from: myAccount[0] })
        .then((e) => {
          console.log(e.status);
          if (e.status) {
            console.log("setapprovalforall Successfully!", approved);
          } else {
            console.log("XXXX======>", "ERROR!");
          }
        });
    } catch (err) {
      console.log(err);
    }
  });

  return (
    <>
      <div className="staking_bearx">
        <div className="staking_bearx_body">
          <div className="staking_bearx_body_left">
            <img src={GifIcon} alt="gif" style={{ width: "90%" }} />
          </div>
          <div className="staking_bearx_body_right">
            <div className="staking_intro">
              <div className="staking_intro_title">
                <img src={BearXWorld01} alt="BearXWorld logo" />
                <img src={BearXWorld02} alt="BearXWorld logo" />
              </div>
              <div className="staking_intro_description">
                <p className="normal-text">
                  🐻 Connect and stake your Genesis Bears to continue to
                  generate 10 $ROOT per Bear, per day.
                </p>
                <p className="normal-text">
                  🐻 Connect and stake 5+ Genesis Bears to also generate 50
                  $SROOT per Bear, per day.
                </p>
                <p className="normal-text">
                  🐻 Stake $SROOT and earn $SROOT and $WETH rewards.
                </p>
                <p className="normal-text">
                  🐻 Stake your MiniBearX before February 28th, 2022 @ 4 PM EST
                  and gain beta access to BEARWORLD and qualify for token prize
                  draws (up to 50m tokens to be given away) .
                </p>
              </div>
              {/* <div className="staking_intro_apr">
                {SROOTxTokenAPR.toLocaleString("en-US", {
                  maximumFractionDigits: 3,
                })}{" "}
                % APR
              </div> */}
            </div>
          </div>
        </div>

        <div className="unstaked_bearx">
          <div className="unstaked_bearx_container">
            <div className="unstaked_bearx_title sub-title">
              YOUR UNSTAKED BEARX
            </div>
            {myAccount[0] ? (
              <div className="buttons">
                {bearXOfOwner.length === 0 ? (
                  <></>
                ) : stakepending ? (
                  <>
                    <button
                      type="button"
                      className="button card_font card_fontsize_smaller allbutton"
                      onClick={getbearxofwallet}
                    >
                      Approve ALL
                    </button>
                    <button
                      type="button"
                      className="button card_font card_fontsize_smaller allbutton"
                      disabled
                    >
                      <Spinner
                        as="span"
                        variant="light"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                        animation="border"
                      />
                    </button>
                  </>
                ) : (
                  <>
                    <button
                      type="button"
                      className="button card_font card_fontsize_smaller allbutton"
                      onClick={getbearxofwallet}
                    >
                      Approve ALL
                    </button>
                    <button
                      type="button"
                      className="button card_font card_fontsize_smaller allbutton"
                      onClick={() => {
                        stake(bearXOfOwner, myAccount[0]);
                        // clicked()
                      }}
                    >
                      Stake ALL
                    </button>
                  </>
                )}
              </div>
            ) : (
              <></>
            )}
          </div>

          <div className="unstaked_bearx_description full-width normal-text">
            Click to select a Genesis BearX. Stake your BearX to yield ROOTx,
            SROOTx and rewards in BEARWORLD
          </div>
          <div className="unstaked_bearx_section flex-wrap">
            <>
              {myAccount[0] ? (
                bearxTokenIds.length !== 0 ? (
                  bearxTokenIds.map((_id) => (
                    <BearXCard
                      url={Bear1}
                      title="BearX1"
                      rarity="Common"
                      price="1.5ETH"
                      description="BearX description"
                      account={myAccount[0]}
                      processed={() => {
                        setPending(!pending);

                        console.log("processed");
                      }}
                      clickable={pending}
                      clicked={(state) => setPending(state)}
                      tokenId={_id}
                      key={_id}
                      launchState={launchState}
                      disLaunchState={disLaunchState}
                      loading={loading}
                    />
                  ))
                ) : (
                  <div className="normal-text full-width">
                    You do not have items.
                  </div>
                )
              ) : (
                <div className="align-center">Please connect your wallet.</div>
              )}
            </>
          </div>
        </div>

        <div className="staked_bearx">
          <div className="unstaked_bearx_container">
            <div className="staked_bearx_title sub-title">
              YOUR STAKED BEARX
            </div>
            {myAccount[0] ? (
              <div className="buttons">
                {bearxTokenIdsStaked.length === 0 ? (
                  <></>
                ) : unstakepending ? (
                  <button
                    type="button"
                    className="button card_font card_fontsize_smaller allbutton"
                    disabled
                  >
                    <Spinner
                      as="span"
                      variant="light"
                      size="sm"
                      role="status"
                      aria-hidden="true"
                      animation="border"
                    />
                  </button>
                ) : (
                  <button
                    type="button"
                    className="button card_font card_fontsize_smaller allbutton"
                    onClick={() => {
                      unstake(bearxTokenIdsStaked, myAccount[0]);
                      // clicked()
                    }}
                  >
                    Unstake ALL
                  </button>
                )}
              </div>
            ) : (
              <></>
            )}
          </div>

          <div className="staked_bearx_description normal-text full-width">
            Use multiple BearX to generate more $ROOTx.
          </div>
          <div className="staked_bearx_section flex-wrap">
            <>
              {myAccount[0] ? (
                bearxTokenIdsStaked.length !== 0 ? (
                  bearxTokenIdsStaked.map(
                    (_id) => (
                      <BearXCard
                        url={Bear1}
                        title="BearX1"
                        rarity="Common"
                        price="1.5ETH"
                        description="BearX asg asg as ga sgagasdgasgasgasgasg"
                        staked
                        account={myAccount[0]}
                        processed={() => {
                          setPending(!pending);
                          setBearxBalance(0);
                        }}
                        clicked={() => setPending(true)}
                        // unstake={() => console.log('unstake')}
                        tokenId={_id}
                        key={_id}
                        launchState={launchState}
                        loading={loading}
                      />
                    )
                    // console.log(_id)
                    // return _id
                  )
                ) : (
                  <div className="normal-text full-width">
                    You do not have items.
                  </div>
                )
              ) : (
                <div className="align-center">Please connect your wallet.</div>
              )}
            </>
          </div>
        </div>
      </div>
      <Rewards />
    </>
  );
}

export default BearXSTEAK;
